@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    --subtle-pattern: url(/static/media/topography.fb9a7da3.webp);
    
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

html { scroll-behavior: smooth; } 
body { background-image: url(/static/media/topography.fb9a7da3.webp); background-image: var(--subtle-pattern);}

#innerContent {
    position: relative;
    height: auto;
}

.container  {
    width: 100%;
    max-width: 960px;
    margin: auto;
}

/* Inner Content Navigation */

.site-nav{
    position: absolute;
    top: 0;
    height: 100vh;
    width: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 20px;
    -webkit-align-items: center;
            align-items: center;
    transition: all 0.5s ease-in-out;
    opacity: 0;
}

.site-nav.sticky{
    position: fixed;
    z-index: 100;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    opacity: 1;
}

.site-nav .site-branding{
    padding: 20px;
}
.site-nav ul {
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-row-gap: 50px;
    row-gap: 50px;
    -webkit-justify-content: center;
            justify-content: center;
    height: 100%;
}
.site-nav ul a {
    padding: 20px;
    color: #000;
    text-decoration: none;
}

.site-content{
    margin-left: 80px;
    width: calc( 100% - 80px);
}

.site-content .section {
    padding: 8rem 0;
}

.headline {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 1rem;
}
.subhead {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
}

.body-copy{
    font-size: 1rem;
    line-height: 1.6rem;
    margin-bottom: 1.2rem;
}


@media (max-width: 600px) {
    .App {
        padding: 15px;
    }
    
}
#home {
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    /* background-image: var(--subtle-pattern); */
}

#home h1 {
    font-size: 65px;
    text-transform: uppercase;
    font-weight: 800;
    margin: 0;
    margin-bottom: 10px;
}
#home p{
    font-size: 20px;
    font-weight: 400;
    max-width: 700px;
    margin-bottom: 20px;
}
#home .nav__list{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    list-style: none;
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
    margin-top: 30px;
}
#home .nav__list a{
    text-decoration: none;
    color: #000;
    text-align: center;
    
}

#home .nav__list a .icon-text{
    background-color: #000;
    color: #fff;
    padding:2px 5px;
    font-size: 10px;
    border-radius: 20px;
}
#home .nav__list a .hover-icon, #home .nav__list a .icon-text{
    transition: all .5s ease-in-out;
}
#home .nav__list a .icon-text{
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    display: block;
}
#home .nav__list a .icon-text::before {
    content: "";
    position: absolute;
    top: -5px;
    left: 50%;
    width: 0;
    border-bottom: 7px solid black;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
#home .nav__list a:hover .icon-text{
   opacity: 1;-webkit-transform: translateY(5px);transform: translateY(5px);
}
#home .nav__list a .icon-text{
    display: block;
}


.scroll__down{
    position:absolute;
    bottom:2.5rem;
    left: 0;
    width: 100%;
  }
.scroll__down a{
    text-decoration: none;
  }
  
  .home__scroll-name{
    font-size: 12px; text-decoration: none; color: #000;
  }
  
  .mouse{
    border: 2px solid #000;
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
  }
  
  .wheel{
    background-color: #000;
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    -webkit-animation: ani-mouse 2s linear infinite;
            animation: ani-mouse 2s linear infinite;
  }
  
  @-webkit-keyframes ani-mouse {
    0%{
     top: 29%;
    }
    15%{
     top: 50%;
    }
    50%{
     top: 50%;
    }
    100%{
     top: 29%;
    }
   
  }
  
  @keyframes ani-mouse {
    0%{
     top: 29%;
    }
    15%{
     top: 50%;
    }
    50%{
     top: 50%;
    }
    100%{
     top: 29%;
    }
   
  }
.container {
    width: 100%;
    max-width: 1000px;
    height: 100vh;
    margin: 0 auto;
    text-align: center;
  }
  .container footer {
    position: absolute;
    bottom: 25px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    font-family: "Lato", sans-serif;
  }
  
  .container main {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    font-family: "Lato", sans-serif;
    margin-top: -35px;
  }
  .container main h1 {
    font-weight: 400;
    text-transform: uppercase;
    color: #8e8883;
  }
  
  .socialmenu {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
  }
  .socialmenu a {
    font-size: 40px;
    text-decoration: none;
    color: #743f6d;
    transition: all 0.2s;
  }
  .socialmenu a:hover {
    color: #883a7f;
  }
